import wx from 'weixin-js-sdk'
import request from './request.js'

async function wxshare(data){
	let url = 'http://jx.onapp.top/wxshare/wxConfigInfo';
	url = 'https://o400l85452.imdo.co/wxshare/wxConfigInfo'
	let link = window.btoa(location.href.split('#')[0]);
  await request({
		url:url,
		method:'post',
		data:{url:link},
	}).then((res)=>{
		let wxConfigInfo = res.data.data;
		console.log(wxConfigInfo)
		wx.config({
			debug: false,// 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
			appId: wxConfigInfo.appid,         // 必填，公众号的唯一标识，填自己的！
			timestamp: wxConfigInfo.timestamp, // 必填，生成签名的时间戳，刚才接口拿到的数据
			nonceStr: wxConfigInfo.nonceStr,   // 必填，生成签名的随机串
			signature: wxConfigInfo.signature, // 必填，签名，见附录1
			jsApiList: [
				'updateTimelineShareData',
				'updateAppMessageShareData'
			]
		})

		wx.ready(function () {
			//分享到朋友圈
			wx.updateTimelineShareData({
				title: data.title,   // 分享时的标题
				link: wxConfigInfo.url+'#'+data.url,     // 分享时的链接
				imgUrl: data.imgUrl,    // 分享时的图标
				success: function () {
					console.log("分享成功");
				},
				cancel: function () {
					console.log("取消分享");
				}
			});
			//分享给朋友
			wx.updateAppMessageShareData({
				title: data.title,
				desc: data.desc, 
				link: wxConfigInfo.url+'#'+data.url,
				imgUrl: data.imgUrl,
				success: function () {
					console.log("分享成功");
				},
				cancel: function () {
					console.log("取消分享");
				}
			});
	})


	})
}



export default wxshare